.searchPage__logo{
    object-fit: contain;
    height: 50px;
    margin-right: 50px;
}

.searchPage__optionsRight, .searchPage__optionsLeft{
    display: flex;
}

.searchPage__options a, .searchPage__optionsRight a{
    text-decoration: none;
    color: grey;
}

.searchPage__results{
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
}

.searchPage__optionsRight{
    margin-left: 80px;
}

.searchPage__option a{
    margin-left: 5px;
}

.searchPage__option{
    display: flex;
    align-items: center;
    margin: 20px;
}

.searchPage__options{
    display: flex;
    align-items: center;
    color: gray;
}

.searchPage__header{
    display: flex;
    position: sticky;
    top:0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
}

.searchPage__headerBody > .search > .search__input{
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
}

